        <p class="hero-subtitle big">Welcome to BITCAT Universe!<br>Not just a MEME! We have value</p>
        <div class="contract-address">
          <label>Contract Address</label>
          <div class="input-wrapper">
            <input 
              type="text" 
              :value="contractAddress" 
              readonly 
              @click="copyToClipboard"
              ref="contractInput"
            >
            <button class="copy-button" @click="copyToClipboard">
              <i class="fas fa-copy"></i>
            </button>
          </div>
          <div class="copy-tooltip" :class="{ 'show': showCopyTooltip }">
            Copied!
          </div>
