<template>
  <section class="social-links-section">
    <div class="social-links-grid">
      <a :href="link.url" class="social-link" :class="{'accented': isAccent}" v-for="(link, index) in socialLinks" :key="index" target="_blank" ref="socialLinks">
        <canvas :ref="'canvas-' + index" class="fire-border"></canvas>
        <img :src="link.icon" :alt="link.name">
      </a>
    </div>
    <div class="buy-buttons">
      <a href="https://jup.ag/swap/SOL-4j9bDg7iWNah1Qa61rrqwWZMtEdqV3fV56SzyhfNpump" target="_blank" class="buy-now-button">
        <span>Buy Now</span>
        <span class="paw"><i class="fa-solid fa-paw"></i></span>
      </a>
      <a href="https://app.defx.com/trade/spot/4j9bDg7iWNah1Qa61rrqwWZMtEdqV3fV56SzyhfNpump" target="_blank" class="buy-now-button">
        <span>Buy with Leverage</span>
      </a>
    </div>
  </section>
</template>

<script>
export default {
  name: 'SocialLinksSection',
  data() {
    return {
      socialLinks: [
        { name: 'Twitter', icon: 'https://solanabitcat.com/data/x.png', url: 'https://x.com/bitcat_sol_' },
        { name: 'Telegram Channel', icon: 'https://solanabitcat.com/data/tg.png', url: 'https://t.me/SolanaBitcat' },
        { name: 'Telegram Chat', icon: 'https://solanabitcat.com/data/tgcn.png', url: 'https://t.me/BitCatCN_CTO' },
        { name: 'Discord', icon: 'https://solanabitcat.com/data/discord.png', url: 'https://discord.com/invite/59HFjFc9G4' },
        { name: 'Geckoterminal', icon: 'https://solanabitcat.com/data/cg.png', url: 'https://www.coingecko.com/en/coins/bitcat-3' },
        { name: 'Dexscreener', icon: 'https://solanabitcat.com/data/cmc.png', url: 'https://coinmarketcap.com/dexscan/solana/EJUgQQABcKXEiERGkUFXM45swGvSFoi8p7fnAcyErTDK/' },
        { name: 'Dextools', icon: 'https://solanabitcat.com/data/dex.png', url: 'https://dexscreener.com/solana/ejugqqabckxeiergkufxm45swgvsfoi8p7fnacyertdk' },
        { name: 'Birdeye', icon: 'https://solanabitcat.com/data/tools.png', url: 'https://www.dextools.io/app/en/solana/pair-explorer/EJUgQQABcKXEiERGkUFXM45swGvSFoi8p7fnAcyErTDK?t=1738855022577' },
        { name: 'Raydium', icon: 'https://solanabitcat.com/data/be.png', url: 'https://birdeye.so/token/4j9bDg7iWNah1Qa61rrqwWZMtEdqV3fV56SzyhfNpump?chain=solana' },
      ],
      particles: [],
      canvasContexts: [],
      hoverStates: new Array(9).fill(false)
    }
  },
  props: {
    isAccent: {
      type: Boolean,
      default: false
    }
  },
  mounted() {
    if (this.isAccent) {
      this.$nextTick(() => {
        this.initFireEffects()
      })
    }
  },
  beforeUnmount() {
    if (this.animationFrames) {
      this.animationFrames.forEach(id => cancelAnimationFrame(id))
    }
  },
  methods: {
    initFireEffects() {
      this.socialLinks.forEach((_, index) => {
        const canvas = this.$refs['canvas-' + index][0]
        const ctx = canvas.getContext('2d')
        const link = this.$refs.socialLinks[index]
        
        canvas.width = link.offsetWidth
        canvas.height = link.offsetHeight
        
        this.canvasContexts[index] = ctx
        this.particles[index] = []
        
        link.addEventListener('mouseenter', () => {
          this.hoverStates[index] = true
        })
        link.addEventListener('mouseleave', () => {
          this.hoverStates[index] = false
        })
        
        this.createParticles(index)
        this.animate(index)
      })
    },
    createParticles(index) {
      const canvas = this.$refs['canvas-' + index][0]
      const particleCount = 150
      const centerX = canvas.width / 2
      const centerY = canvas.height / 2
      const radius = (Math.min(canvas.width, canvas.height) / 2) - 8
      
      for (let i = 0; i < particleCount; i++) {
        const angle = (Math.PI * 2 / particleCount) * i
        this.particles[index].push({
          x: centerX + Math.cos(angle) * radius,
          y: centerY + Math.sin(angle) * radius,
          angle: angle,
          radius: Math.random() * 2 + 1.5,
          speed: 0.005 + Math.random() * 0.01,
          baseRadius: radius + (Math.random() * 3 - 1.5),
          hue: Math.random() * 20 + 25,
          alpha: 0.6 + Math.random() * 0.4
        })
      }
    },
    animate(index) {
      const ctx = this.canvasContexts[index]
      const canvas = this.$refs['canvas-' + index][0]
      const centerX = canvas.width / 2
      const centerY = canvas.height / 2
      
      ctx.clearRect(0, 0, canvas.width, canvas.height)
      
      const glowGradient = ctx.createRadialGradient(
        centerX, centerY, 0,
        centerX, centerY, canvas.width / 2
      )
      glowGradient.addColorStop(0, 'rgba(255, 152, 0, 0.1)')
      glowGradient.addColorStop(0.5, 'rgba(255, 87, 34, 0.05)')
      glowGradient.addColorStop(1, 'transparent')
      
      ctx.fillStyle = glowGradient
      ctx.fillRect(0, 0, canvas.width, canvas.height)
      
      this.particles[index].forEach((particle, i) => {
        const speedMultiplier = this.hoverStates[index] ? 3 : 1
        particle.angle += particle.speed * speedMultiplier
        
        particle.x = centerX + Math.cos(particle.angle) * particle.baseRadius
        particle.y = centerY + Math.sin(particle.angle) * particle.baseRadius
        
        particle.x += (Math.random() - 0.5) * 1
        particle.y += (Math.random() - 0.5) * 1
        
        ctx.beginPath()
        const gradient = ctx.createRadialGradient(
          particle.x, particle.y, 0,
          particle.x, particle.y, particle.radius * 2
        )
        gradient.addColorStop(0, `hsla(${particle.hue}, 100%, 60%, ${particle.alpha})`)
        gradient.addColorStop(0.5, `hsla(${particle.hue - 10}, 100%, 50%, ${particle.alpha * 0.5})`)
        gradient.addColorStop(1, 'transparent')
        
        ctx.fillStyle = gradient
        ctx.arc(particle.x, particle.y, particle.radius * 2, 0, Math.PI * 2)
        ctx.fill()
      })
      
      this.animationFrames = this.animationFrames || []
      this.animationFrames[index] = requestAnimationFrame(() => this.animate(index))
    }
  }
}
</script>

